import React from 'react';

import {Switch, Route} from "react-router-dom";

import AppContainer from "./AppContainer";
import QuizContainer from "./QuizContainer";
import StartScreen from '../components/StartScreenv2';
import GameOver from "../components/GameOverV2";
import BuyTicketContainer from "./BuyTicketContainer";

import "../css/button.css";
import "../css/startScreen.css";

const backgroundImage = `url('${process.env.PUBLIC_URL}/Background_main.png')`;

const StartPage = () => (
    <Switch>
        <Route exact path="/">
            <StartScreen background={backgroundImage} />
        </Route>
        <Route path="/game">
            <AppContainer background={backgroundImage} />
        </Route>
        <Route path="/quiz">
            <QuizContainer background={backgroundImage} />
        </Route>
        <Route path="/buy-ticket">
            <BuyTicketContainer background={backgroundImage} />
        </Route>
        <Route path="/game-over">
            <GameOver background={backgroundImage} />
        </Route>
    </Switch>
);

export default StartPage;