import React from "react";
import classNames from "classnames";

import "../../css/button.css";

const PointButton = ({children, className}) => {
    let buttonBackground = {
        "backgroundImage": `url('${process.env.PUBLIC_URL}/Button_points-02.png')`
    };

    return (
        <div className={classNames("button_point-wrapper", className)} style={buttonBackground}>
            <button className="button_point">{children}</button>
            <span className="button_point-point">+10</span>
        </div>
    );
};

export default PointButton;